.header_outer_container{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: rgb(148, 132, 132); */
    background-color: rgb(20, 20, 20);
    /* background-color: transparent; */
    height: 10vh;
    color: white;
    transition: all 0.5s;
    transition-timing-function: ease-in;
    z-index: 1;
    /* background-color: red; */
    
}

.nav_black{
    background-color: rgb(20, 20, 20);
}

.header_container{
   width: 90%; 
   display: flex;
   justify-content: space-between;
   align-items: center;
   /* background-color: yellow; */
}

.header_left ul{
    width: 140%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* text-decoration: none; */
}

.header_right ul{
    width: 140%;
    display: flex;
    justify-content: space-between;
}

.header_left ul li{
    list-style: none;
}

.header_right ul li{
    list-style: none;
}